
body {
  font-family: 'Raleway', sans-serif;
  color: white;
  background-color: black;
}
footer {
  display: flex;
  color: white;
  text-align: center;
  align-content: center;
  justify-content: center;
  background-color: black;
}

.hero {
  display: flex;
  background-image: url(./cover/picspree-305681.jpg);
  width: 100%;
  height: 100%;
  color: black;
  text-align: center;
  align-content: center;
  background-color: black;
}
.box {
  background-color: black;
}
.title {
  font-family: 'Raleway', sans-serif;
  color: #DEE2E6;
}

.div.column.is.half {
  width: auto;
  height: auto;
}

.image {
  display: block;
  width: 100%;
  height: auto;
}

.images img {
  display: flex;
  flex-wrap: wrap;
  flex: 1;
  justify-content: space-around;
  background-position: cover;
  margin-right: 10px;
  border-radius: 8px;
  display: block;
  width: 75%;
  height: auto;
  border: black solid transparent;

}

h1.nav-title {
  font-weight: bolder;
  font-size: 50px;
  color: black;
  padding-left: 20px;
  padding-top: 10px;
  font-family: 'Dancing Script', cursive;
}
.h2 {
  color: black;
  font-family: 'Raleway', sans-serif;
}

.nav {
  height: 60px;
}


.navbar-item {
  font-weight: bolder;
  font-size: 20px;
  padding-left:85px;
}

.resume-anchor {
  color: blue;
}


div.box {
  /* display: flex; */
